@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
}
body {
  background: #0575e6; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #021b79,
    #0575e6
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.mynav {
  background-color: #021b79 !important;
}
.navText {
  display: flex;
  justify-content: center;
  align-items: center;
  color: '#30336B';
  font-family: 'Bonbon';
}
.cl1 {
  margin: 8%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.cl2 {
  height: 250px;
  width: 350px;
  background: #0449b1;
  border-radius: 25px;
  box-shadow: inset -3px -3px 5px #0449b1, 3px 3px 15px #00000070;
}

/* ... */
#ip1 {
  border-radius: 25px;
  border: 2px solid #609;
  padding: 20px;
  width: 200px;
  height: 10px;
  margin-top: 10%;
  margin-left: 15%;
}
#ip2 {
  border-radius: 25px;
  border: 2px solid #609;
  padding: 20px;
  width: 200px;
  height: 10px;
  margin-top: 5%;
  margin-left: 20%;
}

.btn-grad {
  background-image: linear-gradient(
    89.7deg,
    rgba(0, 32, 95, 1) 2.8%,
    rgba(132, 53, 142, 1) 97.8%
  );
  width: 40%;
  border: none;
  color: white;
  padding: 12px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 10px;
  cursor: pointer;
  border-radius: 12px;
  margin-left: 28%;
  margin-top: 5%;
}
.btn-grad:hover {
  background-image: linear-gradient(to right, #363795, #005c97);
}

